body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-poppins: Poppins;

  /* font sizes */
  --font-size-3xl: 22px;
  --font-size-5xl: 24px;
  --font-size-41xl: 60px;
  --font-size-13xl: 32px;
  --font-size-base: 16px;

  /* Colors */
  --color-darkturquoise: #39d1dc;
  --color-white: #fff;
  --color-midnightblue: #010851;
  --color-aliceblue: #f5fbff;
  --color-dimgray: #454868;

  /* Border radiuses */
  --br-mini: 15px;
  --br-481xl: 500px;
  --br-81xl: 100px;
}
